import styled from "styled-components";
import TestBoxWrap from "./components/TestBoxWrap";
import TestHeader from "./components/TestHeader";
import IcoCaution from "../../assets/images/ico_caution.svg";
import Button from "../../components/UI/Button";
import { Link } from "react-router-dom";

function MyTestStart() {
    return (
        <TestBoxWrap>
            <div className="header">
                <TestHeader />
            </div>
            <div className="body">
                <InfoBox>
                    <h4 className="caution"></h4>
                    <ul className="inner">
                        <li>
                              30 ,   
                             .     
                                  
                                   
                                 .
                        </li>
                        <li>
                                   
                                .
                               .
                        </li>
                        <li>
                                  
                               .
                        </li>
                        <li>
                                ,  
                                   
                            .
                        </li>
                    </ul>
                </InfoBox>
            </div>
            <div className="btn_wrap">
                <Button className="round h_50">
                    <Link to="/mytest"></Link>
                </Button>
                <Button className="primary round h_50">
                    <Link to="/mytest/:mytestId/detail"></Link>
                </Button>
            </div>
        </TestBoxWrap>
    );
}
const InfoBox = styled.div`
    padding: 4rem 3.2rem;
    text-align: center;
    border-radius: 1.6rem;
    border: 1px solid ${({ theme }) => theme.colors.borderDark};
    background: #f9f9f9;
    position: relative;
    .caution {
        display: flex;
        align-items: center;
        justify-content: center;
        gap: 0.8rem;
        font-size: 2rem;
        font-weight: 400;
        line-height: 3.2rem;
        color: ${({ theme }) => theme.colors.primaryBg};
        margin-bottom: 2.4rem;

        &::before {
            content: "";
            width: 2.2rem;
            height: 2.2rem;
            background: url(${IcoCaution}) no-repeat center;
        }
    }
    .inner {
        font-size: 1.6rem;
        font-weight: 400;
        line-height: 2.4rem;
        li {
            display: flex;
            align-items: center;
            justify-content: center;
            gap: 0.5rem;
            &:nth-child(1)::before {
                content: "1.";
            }
            &:nth-child(2)::before {
                content: "2.";
            }
            &:nth-child(3)::before {
                content: "3.";
            }
            &:nth-child(4)::before {
                content: "4.";
            }
        }
    }
    @media (max-width: ${({ theme }) => theme.breakpoint.mobileM}px) {
        padding: 2.4rem 1.6rem;
        text-align: left;
        .caution {
            font-size: 1.6rem;
        }
        .inner {
            font-size: 1.4rem;
            line-height: 2rem;
            li {
                justify-content: flex-start;
                align-items: flex-start;
            }
        }
        .btn_wrap {
            position: absolute;
            width: 100%;
            bottom: 0;
            left: 0;
            padding: 1.2rem 1.6rem;
            background: #fff;
            z-index: 10;
        }
    }
`;
export default MyTestStart;
